import React, { Component, Fragment, useState, useEffect } from "react";
import DatePicker from "react-datepicker";
import {useSpring, animated} from 'react-spring'
import "react-datepicker/dist/react-datepicker.css";
import { request, dateFormat } from '../helper';
import Button from "@material-ui/core/Button";

const CallBacks= () =>{
  const [startDate, setStartDate] = useState('');
  const [endDate, setEndDate] = useState('');
  const [selectedStart, setSelectedStart] = useState('');
  const [selectedEnd, setSelectedEnd] = useState('');
  const [TcId, setTcId] = useState("");
  const [data, setData] = useState([]);
  const [employeeIds, setEmployeeIds] = useState([]);
  const [ employeeId, setEmployeeId] = useState([]);


  useEffect(async () => {
    const employeeId = localStorage.getItem('_id');
    const res = await request(`employee/role/${employeeId}`).get();
    const json = await res.json();
    setEmployeeIds(json);
    },[])


     let reset = () => {
      setStartDate('');
      setEndDate('');
    }

      let TcIdChange= (e)=>{
        setTcId(e);
      }
  
      let startDateChange = (e) => {
        setStartDate(e);
        setSelectedStart(dateFormat(new Date(e).toLocaleDateString()));
      }
    
      let endDateChange = (e) => {
        setEndDate(e);
        setSelectedEnd(dateFormat(new Date(e).toLocaleDateString()));
      }
     
     const handleSubmit = async (e) =>{
         e.preventDefault();
         var requestObj = { start_date: selectedStart, end_date: selectedEnd, id: TcId };
        
         const data = await request('reports/callbacks').post(requestObj);
         const json =  await data.json();

        if(json == "") {
            alert("No data found");
        }

        setData(json);

        e.target.reset();
        reset();
     }

     const props = useSpring({
      opacity:1,
      from: {opacity:0}
    })

    return(
      <animated.div style={props}>
        <Fragment>
      <section className="mx-auto col-sm-12 p-3 m-0 rounded">
        <h4 className="text-center mx-auto col-sm-6 mb-3 heading p-2">Callback Reports</h4>
        <div className="headTwo pt-4">
        <form onSubmit={handleSubmit}>
          <div className="d-flex justify-content-between mt-3">
            <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
              <label htmlFor="exampleInputEmail1" className="mb-0">Call Back ID</label>
              <input list="suggestions" placeholder="Select TC Id" onChange={(e)=>{TcIdChange(e.target.value)}}/>
              <datalist id="suggestions">
                  {
                        employeeIds.map((employee, i)=>{
                          return(<option key={i} value={employee.employee_id}>{employee.firstname} | {employee.designation} | {employee.branch} </option>)
                       })
                    }
              </datalist>
            </div>
            <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
              <label htmlFor="exampleInputPassword1" className="mb-0">From Date</label>
              <DatePicker className="form-control"  selected={startDate} onChange={date => startDateChange(date)} required placeholderText="Select Date"  />
            </div>
            <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
              <label htmlFor="exampleInputPassword1" className="mb-0">To Date</label>
              <DatePicker className="form-control" selected={endDate} onChange={date => endDateChange(date)} required placeholderText="Select Date" />
            </div>
          </div>
          <div className="d-flex" style={{ width: "100%" }}>
            {/* <input type="submit" className="btn btn-primary d-block btn-sm mx-auto" 
            style={{ borderRadius: "15px", backgroundColor: "rgb(219, 144, 31)", color: "white", border: "none" }}
            /> */}
            <Button className="mx-auto" type="submit" variant="contained" color="primary">Submit</Button>
          </div>
          </form>
        </div>
      </section>

      <table className="table table-bordered">
        <thead>
          <tr>
            <th scope="col">S.No</th>
            <th scope="col">Customer Name</th>
            <th scope="col">Product Name</th>
            <th scope="col">Mobile Number</th>
            <th scope="col">Email ID</th>
            <th scope="col">Location</th>
            <th scope="col">State</th>
            <th scope="col">TC ID</th>
            <th scope="col">TC Name</th>
            <th scope="col">Branch</th>
          </tr>
        </thead>
        <tbody>
          {
            (data !== undefined) &&
            data.map((item, index) => {
              return (
                <tr key={index}>
                  <th scope="row">{index + 1}</th>
                  <td><small>{item.first_name} {item.last_name}</small></td>
                  <td><small>{item.product}</small></td>
                  <td><small>{item.mobile}</small></td>
                  <td><small>{item.email}</small></td>
                  <td><small>{item.location}</small></td>
                  <td><small>{item.present_state}</small></td>
                  <td><small>{item.employee_id}</small></td>
                  <td><small>{item.firstname}</small></td>
                  <td><small>{item.branch}</small></td>
                </tr>
              )
            })
          }
        </tbody>
      </table>
    </Fragment>
    </animated.div>
    );

}

export default  CallBacks;