import React, { Component, Fragment, createRef } from "react";
import Button from "@material-ui/core/Button";
import { Redirect } from "react-router-dom";
import {request, dateFormat} from "../helper";
import { withRouter } from "react-router-dom"; 
import { Search } from "@material-ui/icons";




class SearchCustomer extends Component{

    
enteredMobileNumber = createRef(null)

onSubmitHandler = async (e) => {
    e.preventDefault();

    if(this.enteredMobileNumber.current.value.length !== 10){
        alert("Please Enter 10 Digits Valid Number");
        return;
    }

    const mobileNumber = {
            mobile : this.enteredMobileNumber.current.value,
    }

    const res = await request('existingcustomer/customerdetails').post(mobileNumber);
    const json = await res.json();
    let responseData = null;
    if(res.status === 200){
        responseData = json;
    }


    if(res.status !== 200 || json.length === 0 || !json){
        alert("No Customer with this mobile number");
        return;
    }
    
    this.props.history.push({
        pathname:"/Home/Existingcustomer",
        state: responseData[0]
       });

}

render(){
    return(
        <Fragment>
            <section className="mx-auto col-sm-7 p-3 m-0 rounded">
              <h4 className="text-center mx-auto col-sm-11 mb-3 heading p-2" style={{borderRadius:"50px", border:"1px solid #fff", padding: "10px 30px", boxShadow:" 0 4px 8px 0 rgba(0,0,0,.2), 0 6px 20px 0 transparent"}}>Customer Details</h4>
              <div className="headTwo pt-4" style={{borderRadius:"50px", border:"1px solid #fff", padding: "10px 30px", boxShadow:" 0 4px 8px 0 rgba(0,0,0,.2), 0 6px 20px 0 transparent"}}>
                <form onSubmit={(e) => this.onSubmitHandler(e)}>
                  <div className="form-group pt-0 pb-0 pr-3 pl-3" >
                    <label htmlFor="exampleInputEmail1" className="mb-0"> Enter Customer Mobile Number </label>
                    <input type="text"  name="location" required className="form-control shadow" id="exampleInputEmail1" aria-describedby="emailHelp" placeholder="Mobile Number" ref={this.enteredMobileNumber} autoComplete="off" style={{display:"block", backgroundColor: "White", color: "#495057", lineHeight: "1.5", fontSize: "1rem", borderRadius:"50px", border:"1px solid #fff", padding: "10px 30px", boxShadow:" 0 4px 8px 0 rgba(0,0,0,.2), 0 6px 20px 0 transparent"}}/>
                  </div>
                  <div className="d-flex" style={{ width: "100%" }}>
                    {/* <input type="submit" className="btn d-block btn-sm mx-auto" style={{ borderRadius: "15px", backgroundColor: "rgb(219, 144, 31)", color: "white" }} /> */}
                    <Button className="mx-auto" type="submit" variant="contained" color="primary">Get Details</Button>
                  </div>
                </form>
              </div>
            </section>
        </Fragment>
    );
}
}

export default withRouter(SearchCustomer);