import React, { Fragment } from "react";
import "react-datepicker/dist/react-datepicker.css";
import DatePicker from "react-datepicker";
// import '../validations'
import { request } from "../helper";
import { withRouter } from "react-router-dom";

class AddEmployee extends React.Component {
  constructor (props) {
    super(props)
    this.state = {
      lastdate: '',
      userdate: '',
      employee_id: '',
      title: '',
      firstname: '',
      lastname: '',
      email: '',
      password: '',
      mobile: '',
      gender: '',
      designation: '',
      designations: [],
      branches: [],
      team_lead_id: ''
    }
  }

  componentDidMount() {
    (async () => {
      let desg = await request("designation/all").get();
      let json = await desg.json();

      let bran = await request("branch/all").get();
      let resp = await bran.json();

      this.setState({
        designations: json.message,
        branches: resp.message,
      })
    })();
  }

  // DatePicker
  Changedate = (e) => { this.setState({ lastdate: e, }); };
  ChangeUserdate = (e) => { this.setState({ userdate: e, }); };

  handleChange = event => {
    this.setState({
      [event.target.name]: event.target.value
    })
  }

  check = () => {
    if (isNaN(Number(this.state.employee_id))) {
      alert("Please Enter valid Employee Id");
      return false;
    } else if (isNaN(Number(this.state.mobile))) {
      alert("Please Enter valid Mobile Number");
      return false;
    }

    return true;
  }

  handleSubmit = async (event) => {
    event.preventDefault();
    const data = {
      employee_id: Number(this.state.employee_id),
      title: this.state.title,
      firstname: this.state.firstname,
      lastname: this.state.lastname,
      email: this.state.email,
      password: this.state.password,
      mobile: Number(this.state.mobile),
      gender: this.state.gender,
      designation: this.state.designation,
      location: this.state.location,
      branch: this.state.branch,
      state: this.state.state,
      region: this.state.region,
      role: this.state.role,
      team_lead_id : this.state.givenTeamLeadId
    }

    if (this.check() === false) return;

    let res = await request("employee/add").post(data);
    let text = await res.text();
    // let json = await res.json();

    if(res.status === 200){
      alert("Employee Added Succesfully")
    }
    
    Array.from(document.querySelectorAll("input")).forEach(
      input => (input.value = "")
    );

    this.setState({
      itemvalues: [{}]
    });

    // this.props.history.push({ pathname: `/Home` })
  };

  teamLeadId(){
        if(this.state.role === "Telecaller" || this.state.designation === "Telecaller"){
          return (
            <div className="d-flex justify-content-between">
                <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                <label htmlFor="exampleInputPassword1" className="mb-0">Team Lead ID (Optional)</label>
                  <input type="text" onChange={this.handleChange} name="team_lead_id" className="form-control" placeholder="Team Lead Id" autoComplete="off" />
                </div>
                <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3"></div>
                <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3"></div>
            </div>
          )
        } 
  }

  render() {
    return (
      <Fragment>
        <section className="mx-auto col-sm-12 p-3 m-0 rounded">
          <h4 className="text-center mx-auto col-sm-6 mb-3 heading p-2">Add Employee Details</h4>
          <div className="headTwo pt-4">
            <form onSubmit={this.handleSubmit}>
              <div className="d-flex justify-content-between">
                <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                  <label className="mb-0">Employee ID</label>
                  <input type="text" placeholder="Employee ID" name="employee_id" autoComplete="off"
                    onChange={this.handleChange} className="form-control" required />
                </div>

                <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3 d-flex" style={{ display: "block" }}>
                  <div className="col-sm-4 pr-0 pl-0">
                    <label htmlFor="exampleInputPassword1" className="mb-0">Title</label>
                    <select required className="form-control" name="title" onChange={this.handleChange}>Title
                      <option value="">Select Title</option>
                      <option>Mr</option>
                      <option>Miss</option>
                      <option>Mrs</option>
                    </select>
                  </div>

                  <div className="col-sm-9 pl-0">
                    <label htmlFor="exampleInputPassword1" className="mb-0">First Name</label>
                    <input type="text" onChange={this.handleChange} name="firstname" required className="form-control" placeholder="First Name" autoComplete="off" />
                  </div>
                </div>
                <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                  <label htmlFor="exampleInputPassword1" className="mb-0">Last Name</label>
                  <input type="text" onChange={this.handleChange} name="lastname" required className="form-control" placeholder="Last Name" autoComplete="off" />
                </div>
              </div>

              <div className="d-flex justify-content-between">
                <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                  <label htmlFor="exampleInputEmail1" className="mb-0">Mobile Number</label>
                  <input type="text" onChange={this.handleChange} name="mobile" required className="form-control" aria-describedby="emailHelp" placeholder="Mobile Number" autoComplete="off" />
                </div>
                <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">-
                  <label htmlFor="exampleInputPassword1" className="mb-0">Email</label>
                  <input type="email" onChange={this.handleChange} name="email" required className="form-control" placeholder="Email" autoComplete="off" />
                </div>
                <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                  <label htmlFor="exampleInputPassword1" className="mb-0">Password</label>
                  <input type="password" onChange={this.handleChange} name="password" required className="form-control" placeholder="Password" autoComplete="off" />
                </ div>
              </div>

              <div className="d-flex justify-content-between">
                <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                  <label htmlFor="exampleInputPassword1">Gender</label>
                  <select required className="form-control" onChange={this.handleChange} name="gender">
                    <option value="">select Gender</option>
                    <option>Male</option>
                    <option>Female</option>
                  </select>
                </div>

                <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                  <label htmlFor="exampleInputPassword1" className="mb-0">Last Updated Date</label>
                  <DatePicker required className="form-control" selected={this.state.lastdate} placeholderText="Select Date" showPopperArrow={false}
                    onChange={this.Changedate} onKeyDown={(e) => e.preventDefault()} />
                </div>
                <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                  <label htmlFor="exampleInputPassword1" className="mb-0">User Updated Date</label>
                  <DatePicker required className="form-control" selected={this.state.userdate} placeholderText="Select Date" showPopperArrow={false}
                    onChange={this.ChangeUserdate} onKeyDown={(e) => e.preventDefault()} />
                </div>
              </div>

              <div className="d-flex justify-content-between">
                <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                  <label htmlFor="exampleInputPassword1">Designation</label>
                  <select required className="form-control" onChange={this.handleChange} name="designation" >
                    <option value="">select Designation</option>
                    {
                      this.state.designations.map((item, index) => {
                        return (
                          <option key={item.designation + index} value={item.designation}>
                            {item.designation}
                          </option>
                        )
                      })
                    }
                  </select>
                </div>
                <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                  <label htmlFor="exampleInputPassword1">Location</label>
                  <select required className="form-control" id="location" onChange={this.handleChange} name="location" >
                    <option value="">select Location</option>
                    <option>Hyderabad</option>
                  </select>
                </div>
                <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                  <label htmlFor="exampleInputPassword1">Branch Name</label>
                  <select required className="form-control" onChange={this.handleChange} name="branch" >
                    <option value="">select Branch Name</option>
                    {
                      this.state.branches.map((item, index) => {
                        return (
                          <option key={item.branch + index} value={item.branch}>
                            {item.branch}
                          </option>
                        )
                      })
                    }
                  </select>
                </div>
              </div>

              <div className="d-flex justify-content-between">
                <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                  <label htmlFor="exampleInputPassword1">State Name</label>
                  <select required className="form-control" id="StateName" onChange={this.handleChange} name="state" >
                    <option value="">Select State Name</option>
                    <option>Andhra Pradesh</option>
                    <option>Kerala</option>
                    <option>Karnataka</option>
                    <option>Telangana</option>
                    <option>Tamilnadu</option>
                    <option>New Delhi</option>
                    <option>Haryana</option>
                    <option>Jammu and Kashmir</option>
                    <option>Uttar Pradesh</option>
                    <option>Punjab</option>
                    <option>Uttarakhand</option>
                    <option>Goa</option>
                    <option>Maharashtra</option>
                    <option>Rajasthan</option>
                    <option>Gujarat</option>
                    <option>Bihar</option>
                    <option>Orissa</option>
                    <option>Jharkhand</option>
                    <option>West Bengal</option>
                    <option>Chattisgarh</option>
                    <option>Madhya Pradesh</option>
                    <option>Himachal Pradesh</option>
                    <option>Puducherry</option>
                    <option>Assam</option>
                    <option>Arunachal Pradesh</option>
                    <option>Mizoram</option>
                    <option>Meghalaya</option>
                    <option>Manipur</option>
                    <option>Nagaland</option>
                    <option>Sikkim</option>
                    <option>Tripura</option>
                  </select>
                </div>
                <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                  <label htmlFor="exampleInputPassword1">Region</label>
                  <select required className="form-control" id="roleName" onChange={this.handleChange} name="region">
                    <option value="">Select Region</option>
                    <option value="South">South</option>east central west north
                    <option value="East">East</option>
                    <option value="West">West</option>
                    <option value="North">North</option>
                    <option value="Central">Central</option>
                  </select>
                </div>

                <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                  <label htmlFor="exampleInputPassword1">Role Name</label>
                  <select required className="form-control" id="roleName" onChange={this.handleChange} name="role" >
                    <option value="">Select Role Name</option>
                    <option value="Super Admin">Super Admin</option>
                    <option value="Admin">Admin</option>
                    <option value="Relationship Manager">Relationship Manager</option>
                    <option value="Telecaller">Telecaller</option>
                    <option value="Sr.Relationship Manager">Sr. Relationship Manager</option>
                    <option value="PTL">PTL</option>
                    <option value="HCL">HCL</option>
                    <option value="ITL">ITL</option>
                    <option value="Branch Manager">Branch Manager</option>
                    <option value="Area Manager">Area Manager</option>
                    <option value="Zonal Manager">Zonal Manager</option>
                    <option value="Product Manager">Product Manager</option>
                    <option value="Senior Team Leader">Senior Team Leader</option>
                    <option value="RSM">RSM</option>
                    <option value="Vice President">Vice President</option>
                  </select>
                </div>
              </div>
              {this.teamLeadId()}
              <div className="d-flex" style={{ width: "100%" }}>
                <button type="submit" className="btn btn-primary d-block btn-sm mx-auto" style={{ borderRadius: "15px", backgroundColor: "rgb(219, 144, 31)",marginTop:"50px", marginLeft:"15px", color: "white", border: "none" }}>Submit</button>
              </div>
            </form>
          </div>
       
        </section>
      </Fragment>
    );
  }
}

export default withRouter(AddEmployee);